.catalog-nav__link:hover { color: #ed1c24; }
.catalog-nav__link:hover:after { display: block; }

.catalog-content { margin-top: 3rem; margin-bottom: -5rem; }
.catalog-content .store { margin-bottom: 10rem; }

.store__img { margin-bottom: 2.3rem; height: 10rem; text-align: center; font-size: 0; }
.store__img:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.store__img img { display: inline-block; vertical-align: middle; width: auto; max-height: 100%; }
.store__text { padding-right: 10px; line-height: 1.4; }

.cabinet { background: #ed1c24; padding: 1.3rem 0; }
.cabinet__item { position: relative; text-align: center; font-size: 0; text-align: left; font-size: 1.8rem; height: 5rem; }
.cabinet__item:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.cabinet__item:after { content: ""; position: absolute; right: -15px; top: 0; height: 50px; width: 1px; background: #fff; }
.cabinet__item-in { display: inline-block; vertical-align: middle; }
.cabinet .row > div:last-child > div:last-child .cabinet__item:after { display: none; }
.cabinet .header-help { margin-left: -15px; }
.cabinet .logout { margin-left: 15px; }

.user__name { color: #fff; display: block; }
.user__name i, .user__name span { vertical-align: middle; display: inline-block; }
.user__name i { margin-right: 5px; }
.user__info { margin-top: 0.5rem; margin-bottom: 0; }
.user__info li { display: inline-block; margin-right: 1rem; }
.user__info li a { font-size: 1.4rem; color: #fff; }

.balance__number { color: #fff; display: inline-block; margin-right: 1.5rem; }

.header-help a { display: block; color: #fff; font-size: 1.4rem; margin-bottom: 1rem; }
.header-help a:last-child { margin-bottom: 0; }

.logout i { display: inline-block; vertical-align: middle; margin-left: 1rem; font-size: 1.8rem; }

.service__list li { position: relative; color: #bcc6d4; list-style-type: disc; margin: 1.5rem 0 1.5rem 2rem; }
.service__list li:first-child { margin-top: 0; }
.service__list li a { color: #000; font-size: 1.4rem; }
.service__list li a span { color: #bcc6d4; }
.service__list li a:hover { color: #ed1c24; }
.service__header { font-size: 1.8rem; color: #ed1c24; font-family: "museosanscyrl_700", Arial, Helvetica, sans-serif; }
.service__header + .service__list li:first-child { margin-top: 1.5rem; }

.find { border-top: 1px solid #ddd; }
.find > .row > div:first-child { border-right: 1px solid #ddd; }
.find .form-group { margin-top: 2rem; }
.find .form-group label { margin-bottom: 1.5rem; font-size: 1.8rem; }
.find .form-group .form-control { font-size: 1.8rem; }
.find__item { padding-bottom: 2.5rem; }

.no-scroll { overflow: hidden; -webkit-overflow-scrolling: auto; }

.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }
.slick-list:focus { outline: none; }
.slick-loading .slick-list { background: white slick-image-url("ajax-loader.gif") center center no-repeat; }
.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-list, .slick-track, .slick-slide, .slick-slide img { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; zoom: 1; }
.slick-track:before, .slick-track:after { content: ""; display: table; }
.slick-track:after { clear: both; }
.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }
.slick-slide img { display: block; max-width: 100%; }
.slick-slide.slick-loading img { display: none; }
.slick-slide.dragging img { pointer-events: none; }
.slick-initialized .slick-slide { display: block; }
.slick-loading .slick-slide { visibility: hidden; }
.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-prev, .slick-next { position: absolute; z-index: 3; top: 0; bottom: 0; width: 5rem; text-align: center; background: none; font: 0/0 a; color: transparent; text-shadow: none; background-color: transparent; border: 0; }
.slick-prev:after, .slick-next:after { display: inline-block; vertical-align: middle; width: 34px; height: 93px; content: ""; }

.slick-prev { left: 2rem; }
.slick-prev:after { background-image: url("../img/icons.png"); width: 38px; height: 96px; background-position: -84px 0px; }

.slick-next { right: 2rem; }
.slick-next:after { background-image: url("../img/icons.png"); width: 38px; height: 96px; background-position: 0px 0px; }

.slick-dots { position: absolute; bottom: 25px; margin: 0; left: 46%; text-align: left; white-space: nowrap; }
.slick-dots li { position: relative; display: inline-block; padding: 0 3px; }
.slick-dots li.slick-active button { background: #333; }
.slick-dots li:before { display: none; }
.slick-dots button { display: block; width: 2.5rem; height: 2.5rem; cursor: pointer; font: 0/0 a; color: transparent; text-shadow: none; background-color: transparent; border: 0; border-radius: 50%; background: none; border: 4px solid #fff; }


